body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.i{
    display: flex;
    height: 100vh;
}
.i-left{
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-right{
    flex: 1 1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-intro{
    font-size: 30px;
    font-weight: 500;
}
.i-name{
    font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: blue;
}
.i-title-wrapper{
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}
@-webkit-keyframes move {
    50%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
}
@keyframes move {
    50%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
}
.i-image{
    height:100%;
    width: 80%;
    position: relative;
    object-fit: cover;
    right: -210px;
    
}
.i-bg{
    -webkit-clip-path: polygon(100% 0%, 100% 53%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 53%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: green;
}
.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}
.i-desc{
    font-weight: 300;
}

@media screen and (max-width : 480px) {
    .i{flex-direction: column;}
    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
    .i-image{
        right: -60px;
    }
}
.a{
    height: 100vh;
    display: flex;
    align-items: center;
}
.a-left{
    flex:1 1;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}
.a-right{
    flex:1 1;
}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: gray;

}
.a-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}
.a-award-img{
    width: 220px;
    height: 220px;
    border-radius: 20px;
}
.a-title{
    font-weight: 400;
}
.a-sub{
    margin: 20px 0px;
    font-weight: 400;
}
.a-desc{
    font-weight: 400;
}
.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.a-award-texts{
    width: 70%;
}
.a-award-desc{
    font-weight: 400;
}
.a-award-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}
@media screen and  (max-width:480px) {
    .a{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;

    }
    .a-left {
        width: 100%;

    }
    .a-card{
        height: 30vh;
    }
    .a-card.bg, .a-award{
        display: none;
    }
    .a-right{
        padding: 20px;
    }
    .a-image{
        display: none;
    }
    
}
.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.pl-texts{
    width: 65%;
}
.pl-title{
    font-size: 50px;
    font-weight: 600;

}
.pl-desc{
    margin: 20px 0px;
}
.pl-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}
@media screen and  (max-width:480px) {
    .pl{
        padding:10px;
    }
    .pl-texts{
        width: 100%;
    }
    .pl-desc{
        display: none;
    }
    .pl-title{
        margin-top: 60px;
    }

}
.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    

}
.p-browser{
    height: 20px;
    background-color: rgb(28, 11, 129);
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    z-index: 2;
}
.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}
.p-img{
    width: 100% ;
    transition: all 2s ease;
    height: 100%;
}
.p:hover .p-img{
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
}
@media screen and  (max-width:480px) {
    .p{
        width: 40%;
        height: 20vh;
    }
}

.k{
    height: 100vh;
    position: relative;
}
.k-bg{
    width: 20px;
    height: 100%;
    background-color: green;
    position: absolute;
}
.k-wrapper{
    padding: 50px;
    display: flex;

}
.k-left{
    flex: 1 1;

}
.k-right{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.k-title{
    font-size: 60px;
    width: 80%;

}
.k-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}
.k-icon {
    width: 30px ;
    height: 30px ;
    margin-right: 20px ;
}
.k-desc{
    font-weight: 500;
}
form{
    margin-top: 20px;
}
input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: solid 1px black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    border: none;
    padding: 15px;
    background-color: gray;
    color: white;
    font-weight: 500;
    cursor: pointer;
}
@media screen and (max-width : 480px) {
    .k-wrapper{flex-direction: column;
        padding: 0px 50px;
    }
    .k-title{
        font-size: 30px;
    }
    .k-info-itme{
        margin: 20px 0px;
        width: 100%;
    }
    .k-desc{
        display: none;
    }
    form{
        margin-top: 0px;
        display: flex;
        flex-wrap:wrap;
    }
    input{
        width: 35%;
        height: 40px;
        margin: 10px;
        margin-left: 0px;
    }
    button{
        margin-top: 10px;
    }

}
.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid gray;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.t-icon{
    width: 15px;
    height: 15px;
}
.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}
